<template>
  <nav id="mainnav-container" class="mainnav">
    <div class="mainnav__inner">
      <div class="mainnav__top-content scrollable-content pb-5">
        <div class="mainnav__categoriy py-1">
          <h6 class="mainnav__caption mt-0 px-3 fw-bold">Navigation</h6>
          <ul class="mainnav__menu nav flex-column">
            <template v-for="menu in menus">
              <li
                class="nav-item"
                :key="menu.title"
                v-if="$helper.empty(menu.submenus) && canAccess(menu.title)"
              >
                <router-link
                  :to="menu.url"
                  class="nav-link mininav-toggle collapsed"
                  :class="{ active: menu.url.name == currentRoute }"
                  ><i :class="`${menu.icon} fs-5 me-2`"></i>

                  <span
                    class="nav-label mininav-content ms-1 text-capitalize"
                    >{{ $helper.inputTitle(menu.title) }}</span
                  >
                </router-link>
              </li>
              <li
                class="nav-item has-sub"
                :key="'menu_' + menu.title"
                v-else-if="canAccess(menu.title)"
              >
                <a
                  @click.prevent="showSubMenus(menu.title)"
                  href="javascript:void(0)"
                  :id="menu.title"
                  class="nav-link mininav-toggle collapsed"
                  :class="{ active: menu.url.name == currentRoute }"
                  ><i :class="`${menu.icon} fs-5 me-2`"></i>

                  <span
                    class="nav-label mininav-content ms-1 text-capitalize"
                    >{{ menu.title }}</span
                  >
                </a>
                <ul class="mininav-content nav collapse">
                  <template v-for="(item, i) in menu.submenus" :key="i">
                    <li class="nav-item" v-if="canAccess(menu.title, i)">
                      <router-link :to="item" class="nav-link"
                        ><span class="nk-menu-text text-capitalize">{{
                          $helper.inputTitle(i)
                        }}</span></router-link
                      >
                    </li>
                  </template>
                </ul>
              </li>
            </template>
            <li class="nav-item" v-if="canAccess('front_office')">
              <a
                href="javascript:void(0)"
                class="nav-link mininav-toggle collapsed"
                @click.prevent="getBranches"
                target="_blank"
              >
                <svg
                  class="fs-5 me-2"
                  height="15px"
                  viewBox="0 0 16 16"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="currentColor"
                >
                  <path
                    fill-rule="evenodd"
                    d="M12 1H4a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zM4 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H4z"
                  ></path>
                  <path
                    d="M4 2.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.5.5h-7a.5.5 0 0 1-.5-.5v-2zm0 4a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm3-6a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm3-6a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v4a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-4z"
                  ></path>
                </svg>
                <span class="nav-label mininav-content ms-1 text-capitalize"
                  >Front Office</span
                ></a
              >
            </li>
          </ul>
        </div>
      </div>
      <div class="mainnav__bottom-content border-top pb-2">
        <ul id="mainnav" class="mainnav__menu nav flex-column">
          <li class="nav-item">
            <a
              class="nav-link mininav-toggle collapsed"
              @click.prevent="handleLogout"
              href="javascript:void(0)"
              aria-expanded="false"
            >
              <i class="demo-pli-unlock fs-5 me-2"></i>
              <span class="nav-label ms-1">Logout</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  <bootstrap-modal v-if="modalOpen" @close="toggleModal" modal-size="modal-sm">
    <template #head>
      <span>Select Branch and Continue</span>
    </template>
    <template #body>
      <div class="w-100">
        <loading-dots v-if="!branches.length" />
        <template v-else>
          <div class="form-group mb-3">
            <select
              class="form-control-lg form-select"
              v-model="selectedBranch"
            >
              <option value="null" hidden disabled>Select Branch</option>
              <option
                :value="branch.id"
                v-for="branch in branches"
                :key="branch.id"
              >
                {{ branch.name }}
              </option>
            </select>
          </div>
          <div class="form-group">
            <button
              type="button"
              class="btn btn-primary w-100 btn-lg"
              @click="handleContinue"
              :disabled="!selectedBranch"
            >
              Continue
            </button>
          </div>
        </template>
      </div>
    </template>
  </bootstrap-modal>
</template>
<script>
import BootstrapModal from "@/components/BootstrapModal.vue";
import LoadingDots from "@/components/LoadingDots.vue";
import { menus } from "./menus.js";
export default {
  name: "NavBar",
  components: {
    BootstrapModal,
    LoadingDots,
  },
  data: () => ({
    menus,
    selectedBranch: null,
    branches: [],
  }),
  computed: {
    systemDate() {
      return this.$store.state.system_date;
    },
    currentRoute() {
      //return this.$route.fullPath
      return this.$route.name;
    },
    isReady() {
      return this.loggedUser && Object.keys(this.loggedUser).length > 0;
    },
    permissions() {
      return (
        this.isReady &&
        (this.loggedUser.role.pos_permissions ||
          this.loggedUser.role.permissions)
      );
    },
  },
  methods: {
    handleContinue() {
      const branches = this.$store.state.branches;
      const _branch = branches.find(
        (branch) => branch.id == this.selectedBranch
      );
      this.$store.commit("SET_LOCKED_BLANCH", _branch);
      setTimeout(() => {
        const href = this.$router.resolve({
          name: "OrdersCreator",
          params: { action: "new" },
          //query: { branch: this.selectedBranch.id },
        }).href;
        window.open(href, "_blank");
        return false;
        //location.href = href;
      }, 250);
    },
    getBranches() {
      const lockedBranch = this.$store.state.branch;
      if (lockedBranch && Object.keys(lockedBranch).length > 0) {
        this.selectedBranch = lockedBranch.id;
        this.$store.commit("SET_LOCKED_BLANCH", lockedBranch);
        this.handleContinue();
      } else {
        this.toggleModal();
        if (!this.branches.length) {
          this.$store.commit("SET_REQUEST_FLAG", "GET_BRANCHES");
          this.$http.get("branches/show").then((response) => {
            this.branches = response.data.branches;
          });
        }
      }
    },
    canAccess(menu, submenu = null) {
      if (this.userRole == "ADMINISTRATOR" || !this.loggedUser.role_id) {
        return true;
      } else {
        const permissions = this.permissions.back_office;
        if (!submenu) {
          return permissions[menu] && permissions[menu].accessible == true;
        } else {
          return (
            permissions[menu][submenu] &&
            permissions[menu][submenu].includes("R")
          );
        }
      }
    },
    showSubMenus(id) {
      const elt = document.querySelector(`a#${id}`);
      const parent = elt.parentElement;
      elt.classList.toggle("collapsed");
      const ul = parent.querySelector("ul");
      [...document.querySelectorAll(".mininav-content.nav")].forEach((item) => {
        if (item != ul) {
          item.classList.remove("show");
          item.classList.add("collapsed");
        }
      });

      ul.classList.toggle("show");
      ul.classList.toggle("collapsed");
    },
  },
  mounted() {
    const routes = {};
    menus.forEach((menu) => {
      if (!menu.submenus) {
        routes[menu.url.name] = menu.title;
      } else {
        for (let item in menu.submenus) {
          routes[menu.submenus[item].name] = `${menu.title}.${item}`;
        }
      }
    });
    this.$store.state.routes = routes;
  },
};
</script>
