<script>
import Avatar from "@/components/Avatar.vue";
import { mapState } from "vuex";
import { layoutMixin } from "@/common/global-mixin";
export default {
  name: "AppHeader",
  mixins: [layoutMixin],
  components: {
    Avatar,
  },
  data: () => ({
    userDropdown: false,
    notification: false,
    hasReadAll: false,
    confirmed: false,
    selectedBranch: null,
    selectedContent: "ALL",
    newDay: {
      from: new Date(TODAY)
        .toLocaleString("fr-CA", { timeZone: TIMEZONE })
        .slice(0, 10),
      to: new Date(TODAY)
        .toLocaleString("fr-CA", { timeZone: TIMEZONE })
        .slice(0, 10),
    },
  }),
  computed: {
    ...mapState({
      lockedBranch: (state) => state.branch,
      branches: (state) => state.branches,
    }),
    notifications() {
      return this.loggedUser.alerts || [];
    },
    hasNotifications() {
      return (
        this.notifications.some((item) => item.read_at == null) &&
        !this.hasReadAll
      );
    },
  },
  methods: {
    signout() {
      this.$http
        .post("auth/logout", this.$helper.generateFormData({}))
        .then((response) => {
          if (response.data.status) {
            this.$store.dispatch("logout").then(() => {
              window.location.replace(
                this.$router.resolve({ name: "Login" }).href
              );
            });
          }
        });
    },
    setBranch(branch) {
      this.$store.commit("SET_LOCKED_BLANCH", branch);
      location.reload();
    },
  },
};
</script>
